.about {
  padding-top: 140px;
}
.about .about-me {
  margin: auto;
  display: block;
  width: 200px;
  padding-bottom: 30px;
}
.about .about-icons,
.about .about-title {
  text-align: center;
}
.about .about-icons .about-icon,
.about .about-title .about-icon {
  margin: auto;
  display: inline-block;
  width: 12%;
  padding: 10px 2%;
}
.about .about-icons .cookie,
.about .about-title .cookie {
  width: 9%;
}
